import { ChangeEvent } from "react";
import { DataForDate, generateUid, useFinance } from "./FinanceContext"
import { Button } from "react-bootstrap";
import React from "react";

export const UploadFinanceData = (props: {setActiveDateId: (dateId: string) => void}) =>
{
  const setActiveDateId = props.setActiveDateId;
  const { setDataForDates, setConfig,} = useFinance();

  const uploadJson = async (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const text = await file.text();
      try {
        const data = JSON.parse(text);
        if (data.dataForDates && Array.isArray(data.dataForDates)) {

          const updatedDataForDates = data.dataForDates.map((item: DataForDate) => 
          {
            if(!item.uid)
            {
              item.uid = generateUid();
            }
            return item;
          })

          setDataForDates(updatedDataForDates);
          setActiveDateId(updatedDataForDates[0].uid);
        }
        if(data.config)
        {
          setConfig(data.config)
        }
      } catch (error) {
        console.error('Error parsing JSON:', error);
      }
    }
  };

  const fileInputRef: React.RefObject<HTMLInputElement> = React.createRef();

  const handleButtonClick = () => {
    if(fileInputRef.current)
    {
      fileInputRef.current.click();
    }
  };
  
  return   (
    <>
    <input type="file" 
           accept="application/json" 
           onChange={uploadJson} 
           style={{display: "none"}}
           ref={fileInputRef}
           />
    
    <Button onClick={handleButtonClick}>Upload</Button>
    </>
  )
}

export const DownloadFinanceData: React.FC = () =>
{
    const {dataForDates, config, } = useFinance();

    const downloadJson = () => 
    {
        const data = {dataForDates, config}
        const json = JSON.stringify(data, null, 2); // Pretty print JSON with 2-space indentation
        const blob = new Blob([json], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'financeData.json';
        a.click();
        URL.revokeObjectURL(url);
    }

    return(
        <Button onClick={downloadJson}>Download</Button>
    )
};

export default DownloadFinanceData;